import { useState } from "react";

/**
 * @param {{ 'defaultIndex': number }} props
 */

function DropBox(props) {
  const [currentIndex, setCurrentIndex] = useState(props.defaultIndex);
  const [expanded, setExpanded] = useState(false);
  let children = props.children || [];

  const headStyle = props.headStyle;
  const bodyStyle = props.bodyStyle;
  const itemStyle = props.itemStyle;
  const iconStyle = props.iconStyle;

  if (!(children instanceof Array)) {
    children = [children];
  }

  const onClick = (index) => {
    if (props.onClick !== undefined) {
      return props.onClick[index]();
    } else {
      return;
    }
  };

  return (
    <div className="drop-box" tabIndex={1} onBlur={() => setExpanded(false)}>
      <div
        style={headStyle}
        className="head"
        onClick={() => {
          setExpanded(!expanded);
        }}
      >
        {children[currentIndex]}
        {props.icon && (
          <img style={iconStyle} alt="selectIcon" src={props.icon}></img>
        )}
      </div>
      {expanded && (
        <div style={bodyStyle} className="body">
          {children.map((v, i) => (
            <div
              key={i}
              style={itemStyle}
              className="item"
              onClick={() => {
                setCurrentIndex(i);
                setExpanded(false);
                onClick(i);
              }}
            >
              {v}
            </div>
          ))}
        </div>
      )}
    </div>
  );
}
export default DropBox;
