import { Fragment, useState, useReducer } from "react";
import AddButton from "../components/AddButton";
import addButtonIcon from "../images/plus_button.png";
import deleteButtonIcon from "../images/minus_button.png";
import eightMMIcon from "../images/8mm_black.png";
import sixteenMMIcon from "../images/16mm_black.png";
import DropBox from "./DropBox";
import selectIcon from "../images/select_icon2.png";

function PortInfo() {
  const [, forceUpdate] = useReducer((x) => x + 1, 0);
  const [portList, setPortList] = useState([]);

  let count = portList.length + 1;

  const onAddButtonClick = () => {
    const port = {
      number: count++,
      socketType: undefined,
      qrCode: undefined,
    };

    setPortList([...portList, port]);
  };

  const onDeleteButtonClick = () => {
    setPortList(portList.slice(0, portList.length - 1));
  };

  const PortList = () => {
    const PortRow = (props) => {
      const headStyle = {
        boxSizing: "border-box",
        width: "100%",
        height: "30px",
        backgroundColor: "rgb(255, 255, 255)",
        border: "1px solid #D9D9D9",
        fontFamily: "'Noto Sans KR', sans-serif",
        fontWeight: 400,
        fontSize: "12px",
        padding: "0 8px",
      };

      const bodyStyle = {
        boxSizing: "border-box",
        border: "1px solid #252525",
        padding: "3px 0",
        width: "100%",
        top: "2em",
        background: "white",
      };

      const itemStyle = {
        padding: "0 8px",
        fontFamily: "'Noto Sans KR', sans-serif",
        fontWeight: 400,
        fontSize: "12px",
      };

      const iconStyle = {
        width: 12,
        height: 7.2,
      };

      return (
        <div className="portList-container">
          <div>
            <label htmlFor="portNumber">포트넘버</label>
            <input
              className="shortInput portNumber"
              defaultValue={props.info.number}
              name="port_numb[]"
            />
          </div>
          <div>
            <label>단자모양</label>
            <DropBox
              defaultIndex={props.info.socketType}
              headStyle={headStyle}
              bodyStyle={bodyStyle}
              icon={selectIcon}
              iconStyle={iconStyle}
              itemStyle={itemStyle}
              onClick={props.onClick}
            >
              <div style={{ marginBottom: "2px" }}>
                <img
                  style={{ marginRight: "5px" }}
                  src={eightMMIcon}
                  alt="eightMMIcon"
                ></img>
                <span style={{ position: "relative", top: "-1.3px" }}>
                  DC 8mm
                </span>
              </div>
              <div>
                <img
                  style={{ marginRight: "5px" }}
                  src={sixteenMMIcon}
                  alt="sixteenMMcon"
                ></img>
                <span style={{ position: "relative", top: "-1.5px" }}>
                  항공 16mm
                </span>
              </div>
            </DropBox>
            <input
              style={{ display: "none" }}
              defaultValue={(() => {
                if (props.info.socketType === undefined) {
                  return 1;
                } else {
                  return props.info.socketType + 1;
                }
              })()}
              name="port_type[]"
            ></input>
          </div>
          <div>
            <label htmlFor={`QR_value${props.info.number}`}>QR 코드 값</label>
            <input
              className="shortInput"
              id={`QR_value${props.info.number}`}
              defaultValue={props.info.qrCode}
              onChange={(e) => {
                props.info.qrCode = e.currentTarget.value;
              }}
              name="port_code[]"
            />
          </div>
        </div>
      );
    };

    return (
      <Fragment>
        {portList.map((v, i) => {
          const set8mm = () => {
            portList[i].socketType = 0;
          };

          const set16mm = () => {
            portList[i].socketType = 1;
          };

          const handleDropBox = [set8mm, set16mm];

          return <PortRow key={i} info={v} onClick={handleDropBox} />;
        })}
      </Fragment>
    );
  };

  const Buttons = () => {
    return (
      <div className="portInfoButton-container">
        <img
          src={addButtonIcon}
          onClick={onAddButtonClick}
          alt="addButton"
        ></img>
        <img
          src={deleteButtonIcon}
          onClick={onDeleteButtonClick}
          alt="addButton"
        ></img>
      </div>
    );
  };

  return (
    <div className="portInfo-container">
      <Buttons />
      <PortList />
      <div
        style={{
          display: "flex",
          justifyContent: "flex-end",
          marginTop: "30px",
        }}
      >
        <AddButton onClick={forceUpdate} />
      </div>
    </div>
  );
}

export default PortInfo;
