import RadioStationType from "./RadioStationType";
import PortInfo from "./PortInfo";
import { useRef, useState } from "react";

function AddStation() {
  const ImageFile = () => {
    const [filePath, setFilePath] = useState();

    const picture = useRef();

    const onChange = (e) => {
      setFilePath(e.currentTarget.value);
    };

    return (
      <>
        <label htmlFor="addPicture">사진 등록</label>
        <div
          style={{ display: "flex", alignItems: "center", cursor: "pointer" }}
          className="longInput"
          onClick={() => picture.current.click()}
        >
          <span style={{ position: "relative", top: "-1px" }}>{filePath}</span>
        </div>
        <input
          style={{ display: "none" }}
          ref={picture}
          className="longInput"
          id="addPicture"
          type="file"
          accept=".jpg, .png, .gif, .svg"
          onChange={onChange}
          name={picture}
        ></input>
      </>
    );
  };

  return (
    <div className="addStation-container">
      <div className="addStation-title">스테이션 등록</div>
      <form method="POST" className="addStation-form">
        <label htmlFor="stationName">스테이션 명</label>
        <input className="longInput" id="stationName" name="name"></input>
        <label htmlFor="stationCode">스테이션 코드</label>
        <input className="longInput" id="stationCode" name="code"></input>
        <label htmlFor="loginId">로그인 아이디</label>
        <input className="longInput" id="loginId" name="id"></input>
        <label htmlFor="installDate">설치 일자</label>
        <input
          className="longInput"
          id="installDate"
          name="install_date"
        ></input>
        <label htmlFor="latitude">위도</label>
        <input className="longInput" id="latitude" name="lat"></input>
        <label htmlFor="longitude">경도</label>
        <input className="longInput" id="longitude" name="lng"></input>
        <label htmlFor="installAddress">설치 주소</label>
        <input className="longInput" id="installAddress" name="address"></input>
        <ImageFile />
        <label htmlFor="installCompany">설치 업체</label>
        <input className="longInput" id="installCompany" name="admin"></input>
        <RadioStationType />
        <PortInfo />
      </form>
    </div>
  );
}

export default AddStation;
