import { useState } from "react";

function RadioStationType() {
  const [stationType, setStationType] = useState("water");

  const stationTypeChanged = (e) => {
    if (e.currentTarget.value === "1") {
      setStationType("water");
    } else if (e.currentTarget.value === "2") {
      setStationType("sun");
    }
  };

  const RadioButtons = () => {
    return (
      <div
        className="radio-container"
        style={{ display: "flex", marginBottom: "4px" }}
      >
        <input
          name="type"
          id="water"
          type="radio"
          value="1"
          onChange={stationTypeChanged}
          defaultChecked={stationType === "water" ? 1 : 0}
        ></input>
        <label htmlFor="water">수전</label>
        <input
          name="type"
          id="sun"
          type="radio"
          value="2"
          onChange={stationTypeChanged}
          defaultChecked={stationType === "sun" ? 1 : 0}
        ></input>
        <label htmlFor="sun">태양광</label>
      </div>
    );
  };

  const BoxByRadio = () => {
    if (stationType === "water") {
      return (
        <>
          <div>
            <label htmlFor="powerGenerationType">발전 방식</label>
            <input
              defaultValue="수전"
              disabled
              className="shortInput"
              id="powerGenerationType"
            ></input>
          </div>
          <div>
            <label htmlFor="SMPS_spec">SMPS 스펙</label>
            <input className="shortInput" id="SMPS_spec" name="smps"></input>
          </div>
          <div></div>
        </>
      );
    } else if (stationType === "sun") {
      return (
        <>
          <div>
            <label htmlFor="powerGenerationType">발전 방식</label>
            <input
              defaultValue="태양광"
              disabled
              className="shortInput"
              id="powerGenerationType"
            ></input>
          </div>
          <div>
            <label htmlFor="panelSpec">패널 스펙</label>
            <input className="shortInput" id="panelSpec" name="panel"></input>
          </div>
          <div>
            <label htmlFor="batterySpec">배터리 스펙</label>
            <input
              className="shortInput"
              id="batterySpec"
              name="battery"
            ></input>
          </div>
        </>
      );
    }
  };

  return (
    <>
      <RadioButtons />
      <div className="boxByRadio-container">
        <BoxByRadio />
      </div>
    </>
  );
}

export default RadioStationType;
